<template>
  <div class="warp">
    <HEADER></HEADER>
    <div class="con">
      <div class="canvas">
        <div class="progress">
          <ul>
            <li v-for="(item,index) in reportResult.items" :key="index">
              <div class="name">{{item.projectName}}</div>
              <div class="prog">
                <div class="box" v-for="num in item.projectLevelValue+1" :key="num"
                :style="comColor(num)"
                ></div>
                <div class="info">
                  <p>{{item.projectSymbolName}}</p>
                </div>
              </div>
            </li>
          </ul>
        </div>
        <div class="sign">
          <div class="box">
            <div class="prog"></div>
            <div class="info">
              <div class="bad">异常</div>
              <div class="good">良好</div>
            </div>
          </div>
        </div>
      </div>
      <div class="result">
        <ul>
          <li v-for="(item,index) in reportResult.items" :key="index">
            <div class="name">{{item.projectName}}</div>
            <div>{{item.projectSymbol?item.projectSymbol:'&nbsp;'}}</div>
            <div>{{item.projectValue+item.projectUnit}}</div>
          </li>
        </ul>
        <div class="footer">
          <p>{{regForm(reportResult.text)[0]}}</p>
          <p>{{regForm(reportResult.text)[1]}}</p>
          <!-- <p>本数据由尿液检测数据分析软件</p> -->
          <!-- <p>证件号：桂械注准20182700031提供</p> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HEADER from '@/components/header.vue'
import {getHistoryDetialUsingPOST} from '@/../client/javascript/vue-api-client.js'
  export default {
    name: '',
    components: {HEADER},
    data() {
      return {
        reportResult: this.$store.state.reportHealth,
        colorBox: ['#376b98', '#597a92', '#8a918a', '#b6a483', '#dfb77b', '#f4c177']
      }
    },
    methods: {
      state (num) {
        switch (num) {
          case num=0:
            return '良好';
          case num=1:
            return '良好';
          case num=2:
            return '良好';
          case num=3:
            return '阳性';
          case num=4:
            return '强阳性';
        }
      },
      comColor (n) {
        return {
          background: '-webkit-linear-gradient(left, '+this.colorBox[n-1]+ ', '+this.colorBox[n]+');', /* Safari 5.1 to 6.0 */
          background: '-o-linear-gradient(right, '+this.colorBox[n-1]+', '+this.colorBox[n]+');', /* Opera 11.1 to 12.0 */
          background: '-moz-linear-gradient(right, '+this.colorBox[n-1]+', '+this.colorBox[n]+');', /* Firefox 3.6 to 15 */
          background: 'linear-gradient(to right, '+this.colorBox[n-1]+' , '+this.colorBox[n]+')'
        }
      },
      getHistoryDetialUsingPOST () {
        let that = this
        getHistoryDetialUsingPOST ({
          param: {
            resultId: this.$store.state.healthDetail.resultId,
            viewType: "urine_details"
          }
        }).then(response => {
          if(response.data.code === 0){
            that.$store.commit('setReportHealth', response.data.body)
          }
        })
      },
      regForm(text) {
        let line = text.split('\n')
        return line
      }
    },
    computed: {
      // comColor () {
      //   return {
      //     // console(num);
      //     background: 'linear-gradient(to right, #376b98 , #f4c177)'
      //   }
      // }
    }
  }
</script>

<style lang="less" scoped>
.warp {
  height: 100%;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-around;
  .con {
    height: 100%;
    display: flex;
    flex: 1;
    flex-direction: column;
    margin-top: 40px;
    border-bottom: 2px rgb(240, 240, 240) solid;
    position: relative;
    .canvas {
      display: flex;
      flex: 1.75;
      border-bottom: 2px rgb(240, 240, 240) solid;
      position: relative;
      .progress {
        display: flex;
        flex: 6.5;
        ul {
          list-style: none;
          padding: 0;
          margin: 0;
          width: 100%;
          display: flex;
          flex: 1;
          flex-direction: column;
          justify-content: space-around;
          align-items: flex-start;
          li {
            width: 100%;
            height: 3rem;
            margin: 2%;
            display: flex;
            flex: 1;
            justify-content: space-around;
            div {
              display: inline-block;
            }
            .name {
              width: 4rem;
              font-size: 0.85rem;
              font-weight: bold;
              display: flex;
              justify-content: flex-end;
              align-items: center;
              margin-right: 0.7rem;
            }
            .prog {
              width: 100%;
              margin-right: 2%;
              display: flex;
              flex: 1;
              justify-content: flex-start;
              padding: 0.25rem 0;
              .box {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                border-left: 2px solid white;
                width: 16%;
                height: 100%;
                box-sizing: border-box;
                // background: -webkit-linear-gradient(left, #376b98 , #f4c177); /* Safari 5.1 to 6.0 */
                // background: -o-linear-gradient(right, #376b98, #f4c177); /* Opera 11.1 to 12.0 */
                // background: -moz-linear-gradient(right, #376b98, #f4c177); /* Firefox 3.6 to 15 */
                // background: linear-gradient(to right, #376b98 , #f4c177); /* 标准语法 */
              }
              .info {
                width: 16%;
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;
                p {
                  position: absolute;
                  width: 4rem;
                  margin: 0;
                  left: 0;
                  font-size: 0.8rem;
                }
              }
            }
          }
        }
      }
      .sign {
        display: flex;
        flex: 1;
        justify-content: center;
        align-items: center;
        padding-left: 3%;
        .box {
          width: 100%;
          height: 60%;
          display: flex;
          flex: 1;
          .prog {
            display: flex;
            flex: 1;
            background: -webkit-linear-gradient(#f4c177,#376b98); /* Safari 5.1-6.0 */
            background: -o-linear-gradient(#f4c177,#376b98); /* Opera 11.1-12.0 */
            background: -moz-linear-gradient(#f4c177,#376b98); /* Firefox 3.6-15 */
            background: linear-gradient(#f4c177,#376b98); /* 标准语法 */
          }
          .info {
            display: flex;
            flex: 3;
            font-size: 0.5rem;
            flex-direction: column;
            .good {
              color: #376b98;
              display: flex;
              flex: 1;
              justify-content: center;
              align-items: flex-end;
            }
            .bad {
              color: #f4c177;
              display: flex;
              flex: 1;
              justify-content: center;
              align-items: flex-start;
            }
          }
        }
      }
    }
  }
  .result {
    padding: 2%;
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    ul {
      list-style: none;
      display: flex;
      flex: 1;
      justify-content: flex-start;
      align-items: flex-start;
      flex-wrap: wrap;
      align-content: flex-start;
      padding: 0;
      margin: 0;
      width: 100%;
      overflow-y: scroll;
      li {
        width: 25%;
        height: 50px;
        // margin: 5px;
        display: flex;
        flex-direction: column;
        color: rgb(133, 133, 133);
        font-size: 0.75rem;
        .name {
          color: black;
          font-size: 0.85rem;
          font-weight: bold;
        }
      }
    }
    .footer {
      p {
        margin: 0;
        font-size: 0.7rem;
        color: rgb(57, 108, 152)
      }
    }
  }
}
</style>




